.button {
	cursor: pointer;
	font: inherit;
	border: none;
	background-color: #4d1601;
	color: white;
	padding: 0.75rem 3rem;
	display: flex;
	justify-content: space-around;
	align-items: center;
	border-radius: 25px;
	font-weight: bold;
	margin-right: 200px;
}

.button:hover,
.button:active {
	background-color: #2c0d00;
}

.icon {
	width: 1.35rem;
	height: 1.35rem;
	margin-right: 0.5rem;
}

.badge {
	background-color: #b94517;
	padding: 0.25rem 1rem;
	border-radius: 25px;
	margin-left: 1rem;
	font-weight: bold;
}

.button:hover .badge,
.button:active .badge {
	background-color: #92320c;
}

.bump {
	animation: bump 300ms ease-out;
}

@media (max-width: 600px) {
	.button {
		width: 30%;
		font-size: small;
		margin-right: 15%;
	}
}
